import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import dayjs from 'dayjs';
import quarterOfYear from 'dayjs/plugin/quarterOfYear';

import { selectRecords } from '../features/records/recordsSlice';
import { currencyFormat } from '../helpers/currencyFormat';
import Record from './Record';
import Limits from './Limits';
import Trend from './Trend';

import {
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    IconButton,
    FormGroup,
    FormControlLabel,
    Switch,
} from '@mui/material';
import {
    KeyboardArrowDown,
    KeyboardArrowUp,
} from '@mui/icons-material';

import { TRecord } from '../types/records';

dayjs.extend(quarterOfYear);

type TSortedRecords = {
    [key: string]: TRecord[],
}

// type TSplitFormat = 'YYYY' | 'YYYY-MM'
type TSplitFormat = 'year' | 'quarter' | 'month'

type TShowByMonth = {
    [key: string]: boolean,
}

type TPreviousDate = string;

const isQuarter = process.env.REACT_APP_LIMIT_BY_QUARTER === '1';
const lsKeyShowTrends = 'showTrends';

const currentYearMonth = dayjs().format('YYYY-MM');
const currentYearQuarter = `${dayjs().format('YYYY')}-Q${dayjs().quarter()}`;

const filteredRecords = (records: TRecord[]) => {
    return [...records].sort((a, b) => b.date - a.date);
};

const splitRecordsBy = (records, by: TSplitFormat) => {
    return filteredRecords(records).reduce((acc, obj) => {
        let key = '';

        switch (by) {
        case 'month':
            key = dayjs.unix(obj.date).format('YYYY-MM');
            break;
        case 'quarter':
            key = `${dayjs.unix(obj.date).format('YYYY')}-Q${dayjs.unix(obj.date).quarter()}`;
            break;
        case 'year':
            key = dayjs.unix(obj.date).format('YYYY');
            break;
        default:
            break;
        }

        // Create a new array for the key if it does not exist
        const newArray = (acc[key] || []).concat(obj);

        // Return a new object with the new array for the key
        return { ...acc, [key]: newArray };
    }, {});
};

const getSumForRecords = (records: TRecord[]): number => {
    let result = 0;
    records.map((record) => result = result + record.pricePln);
    return Number(result.toFixed(2));
};

const getPreviousDate = (dates: string[], date: string): TPreviousDate => {
    const dateKey = Number(Object.keys(dates).find(key => dates[key] === date));
    if (isNaN(dateKey)) {
        return date;
    }

    if (dateKey < dates.length - 1) {
        return dates[dateKey + 1];
    }

    return date;
};

export default function Records() {
    const records = useSelector<TRecord[]>(selectRecords);
    const [recordsByMonth, setRecordsByMonth] = useState<TSortedRecords>({});
    const [recordsByQuarter, setRecordsByQuarter] = useState<TSortedRecords>({});
    const [recordsByYear, setRecordsByYear] = useState<TSortedRecords>({});
    const [showByMonth, setShowByMonth] = useState<TShowByMonth>({});
    const [dates, setDates] = useState<string[]>([]);
    const [showTrends, setShowTrends] = useState<boolean>(false);

    useEffect(() => {
        const lsShowTrends = localStorage.getItem(lsKeyShowTrends);
        if (lsShowTrends === 'true') {
            setShowTrends(true);
        } else {
            setShowTrends(false);
        }
    }, []);

    useEffect(() => {
        setRecordsByMonth(splitRecordsBy(records, 'month'));
        setRecordsByQuarter(splitRecordsBy(records, 'quarter'));
        setRecordsByYear(splitRecordsBy(records, 'year'));
    }, [records]);

    useEffect(() => {
        if (Object.keys(showByMonth).length === 0) {
            const newShowByMonth = {};
            const keysArray = Object.keys(recordsByMonth);
            setDates(keysArray);

            keysArray.map((key) => {
                newShowByMonth[key] = false;
                return false;
            });

            setShowByMonth(newShowByMonth);
        }
    }, [recordsByMonth]);

    const toggleMonth = (key: string) => {
        const newShowByMonth = { ...showByMonth };
        newShowByMonth[key] = !newShowByMonth[key];
        setShowByMonth(newShowByMonth);
    };

    const handleTrendsShow = () => {
        setShowTrends((prev) => {
            localStorage.setItem(lsKeyShowTrends, String(!prev));
            return !prev;
        });
    };

    return (
        <>
            {!isQuarter && recordsByMonth && Object.keys(recordsByMonth).map((key) => (
                <div key={key} className='mt-0'>
                    {currentYearMonth === key &&
                        <Limits key={key} records={recordsByMonth[key]} />
                    }
                </div>
            ))}

            {isQuarter && recordsByQuarter && Object.keys(recordsByQuarter).map((key) => (
                <div key={key} className='mt-0'>
                    {currentYearQuarter === key &&
                        <Limits key={key} records={recordsByQuarter[key]} />
                    }
                </div>
            ))}

            {recordsByMonth && Object.keys(recordsByMonth).map((key) => (
                <div key={key} className='mt-0'>
                    {currentYearMonth !== key &&
                        <div className='capitalize font-medium py-2'>
                            <IconButton onClick={() => toggleMonth(key)}>
                                {showByMonth[key]
                                    ? <KeyboardArrowUp />
                                    : <KeyboardArrowDown />
                                }
                            </IconButton>
                            <span className='ml-2'>
                                {dayjs(key).format('MMMM YYYY')}
                            </span>
                            <span className='text-slate-500 ml-5 lowercase text-sm'>
                                {currencyFormat(getSumForRecords(recordsByMonth[key]))}
                            </span>
                            {showTrends &&
                                <span className='text-slate-500 ml-2 lowercase text-xs'>
                                    <Trend
                                        current={getSumForRecords(recordsByMonth[key])}
                                        previous={getSumForRecords(recordsByMonth[getPreviousDate(dates, key)])}
                                        down={true} />
                                </span>}
                        </div>
                    }
                    {(currentYearMonth === key || showByMonth[key]) &&
                        <div className='mb-10'>
                            <TableContainer component={Paper}>
                                <Table size="small" aria-label="a dense table">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell>
                                                <div className='w-20 md:w-32 leading-5'></div>
                                            </TableCell>
                                            <TableCell>
                                                <div className='w-20 leading-5'> </div>
                                            </TableCell>
                                            <TableCell>
                                                <div className='w-20 leading-5'></div>
                                            </TableCell>
                                            <TableCell>
                                                <div className='w-20 leading-5'></div>
                                            </TableCell>
                                            <TableCell>
                                                <div className='w-20 leading-5'>  z</div>
                                            </TableCell>
                                            <TableCell>
                                                <div className='w-20 leading-5'></div>
                                            </TableCell>
                                            <TableCell align='right'>
                                                <div className='w-16 leading-5'></div>
                                            </TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {recordsByMonth[key].map((record) => (
                                            <Record key={record.id} {...record} />
                                        ))}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </div>
                    }
                </div>
            ))}

            {Object.keys(recordsByYear).length > 0 &&
                <div className='mt-20 mb-5'>
                    <div className='font-medium mb-3'>  </div>
                    {recordsByYear && Object.keys(recordsByYear).reverse().map((key) => (
                        <div key={key} className='py-3'>
                            {key}
                            <span className='font-medium ml-5'>
                                {currencyFormat(getSumForRecords(recordsByYear[key]))}
                            </span>
                            <div>
                                <span className='text-slate-600 text-sm'>
                                    : {currencyFormat(getSumForRecords(recordsByYear[key].filter((item) => item.reciever === 0)))}
                                </span>
                                <span className='text-slate-600 text-sm ml-5'>
                                    : {currencyFormat(getSumForRecords(recordsByYear[key].filter((item) => item.reciever === 1)))}
                                </span>
                            </div>
                        </div>
                    ))}
                </div>
            }

            <div className='px-4 py-2.5 my-20 border-solid border-2 border-slate-50 rounded-md'>
                <div className='font-medium mb-3'></div>
                <FormGroup>
                    <FormControlLabel control={
                        <Switch checked={showTrends} onChange={handleTrendsShow}/>
                    } label='' />
                </FormGroup>
            </div>
        </>
    );
}
